.homeSection {
  min-height: 250px !important;
  min-width: 100vw;
}

.particles {
  background-image: linear-gradient(to top right, #3629a7, #343a40);
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.intro {
  font-size: 32pt;
  line-height: 36pt;
  text-align: center;
  width: fit-content;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.line {
  width: 60vw;
  background-color: #BBD336;
  height: 3px;
  padding: 0;

  @media screen and (max-width: 800px) {
    width: 87vw;
  }
}


.heroText {

  :nth-child(1) {
    color: #ea2c62;
  }

  :nth-child(3) {
    height: 100px;
    color: #d37815;
  }

  h1 {
    margin: 10px 0 20px 0;
    letter-spacing: -1px;
    font-family: Playfair Display, serif;
    font-weight: 700;
    font-size: 4vw;
    line-height: 1;
    text-align: center;
    color: #fff;
    @media screen and (max-width: 800px) {
      font-size: 6vw;
    }
  }
}

.section {
  height: 100vh;
  width: 100vw;
  position: relative;
  z-index: 2;


  .whiteMask {
    bottom: 0;
    position: absolute;
    z-index: 1;
    left: 50%;
    min-width: 100%;
    height: 400px;
    max-height: 40%;
    display: block;
    transform: translateX(-50%);
  }

  > div {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 300;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100vh;
    min-height: 250px !important;


    @media screen and (max-width: 450px) {
      justify-content: center;

    }
  }
}


.highlight {
  color: #e31b6d;
  font-family: "Raleway-semibold";
}

.welcomeTo {
  width: fit-content;
  height: fit-content;
  @media screen and (max-width: 500px) {
    width: 80%;
  }
}